import React, { useRef, useState } from "react";
import emailjs from "@emailjs/browser";
import styled from "styled-components";
import { motion } from "framer-motion";
import SubTitle from "../components/SubTitle";

const YOUR_SERVICE_ID = "service_rpllskw";
const YOUR_TEMPLATE_ID = "template_fq3wg3f";
const API = "8COq9QAPVe_wIh5BW";

const env = process.env;
env.PUBLIC_URL = env.PUBLIC_URL || "";

const Wrapper = styled(motion.section)`
  width: 100%;
  height: 150vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  position: relative;
  background-image: url("${env.PUBLIC_URL}/assets/page3/Bg_2.png");
  background-position: bottom center;
  background-repeat: no-repeat;
`;

const Container = styled.div`
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
`;

const BigBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  .center {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
  }
`;

const TextBox = styled.div`
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  header {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    h1 {
      font-weight: 700;
      font-size: 80px;
      line-height: 140%;
      text-align: center;
      margin-bottom: 10px;
      margin-top: 50px;
    }
  }
  div {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    p {
      margin-bottom: 20px;
    }
  }
  span {
    font-size: 15px;
    font-weight: 700;
  }
`;

const FormContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-top: 50px;
  position: relative;
  .right {
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }
`;

const InputDiv = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
  margin-top: 10px;
  label {
    font-weight: 800;
    margin-right: 50px;
  }
  input {
    background: #ffffff;
    border: 2px solid #b8b8b8;
    border-radius: 20px;
    width: 516px;
    height: 43px;
    padding: 5px 10px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px,
      rgb(209, 213, 219) 0px 0px 0px 1px inset;
  }
`;

const Btn = styled.button`
  border-style: none;
  border: 3px solid #150b05;
  border-radius: 50px;
  background-color: ${(props) => props.theme.yellow};
  font-size: 40px;
  line-height: 160%;
  width: 388px;
  height: 96px;
  font-weight: bold;
  &:hover {
    transform: translateY(-2px);
  }
`;

const Doneun = styled.img`
  margin-left: 50px;
  margin-bottom: 20px;
`;

const Modal = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  h1 {
    font-size: 30px;
    margin-bottom: 15px;
    margin-top: 50px;
  }
  h3 {
    font-size: 21px;
    font-weight: 300;
  }
`;

const ErrorBox = styled.div`
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-top: 15px;
  p {
    font-size: 13px;
  }
  margin-bottom: 50px;
`;

const Letter = () => {
  const [checkMail, setCheckMail] = useState(false);
  const form = useRef();
  const [subscribe, setSubscribe] = useState(false);
  // const onClick = (e) => {
  //   setSubscribe(!subscribe);
  //   e.stopPropagation();
  // };
  const onCheck = (e) => {
    alert("   ");
    e.stopPropagation();
  };
  const sendEmail = (e) => {
    e.preventDefault();
    emailjs.sendForm(YOUR_SERVICE_ID, YOUR_TEMPLATE_ID, form.current, API).then(
      (result) => {
        console.log(result.text);
      },
      (error) => {
        console.log(error.text);
      }
    );
    setSubscribe(!subscribe);
    e.stopPropagation();
    console.log(form.current);
  };

  const isEmail = (email) => {
    const emailRegex =
      /^(([^<>()\[\].,;:\s@"]+(\.[^<>()\[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
    setCheckMail(emailRegex.test(email.target.value));
    if (checkMail === false) {
      console.log(" ");
    } else {
      console.log(" ");
    }
  };

  return (
    <Wrapper>
      <SubTitle title={" "} />
      {!subscribe ? (
        <Container>
          <TextBox>
            <header>
              <h1>
                 
                <br />   ?
              </h1>
            </header>
          </TextBox>
          <BigBox>
            <div className="center">
              <TextBox>
                <div>
                  <p>
                       
                    <br />     !
                  </p>
                  <p>
                    <span> </span>: 7 14 ~7 19
                    <br />
                    <span></span>: ~200 
                  </p>
                </div>
              </TextBox>

              <FormContainer>
                <form ref={form} onSubmit={sendEmail}>
                  <InputDiv>
                    <label htmlFor="user_name"></label>
                    <input
                      id="user_name"
                      type="text"
                      name="user_name"
                      placeholder="  ."
                      minLength="1"
                      maxLength="15"
                    />
                  </InputDiv>
                  <InputDiv>
                    <label htmlFor="user_email"></label>
                    <input
                      id="user_email"
                      type="email"
                      name="user_email"
                      placeholder="  ."
                      onBlur={isEmail}
                    />
                  </InputDiv>
                  <ErrorBox>
                    <p>
                      {!checkMail
                        ? "*       ."
                        : "  "}
                    </p>
                  </ErrorBox>
                  <div className="right">
                    <Btn
                      type={!checkMail ? "button" : "submit"}
                      onClick={!checkMail ? onCheck : null}
                      style={
                        !checkMail ? { color: "gray" } : { color: "black" }
                      }
                    >
                       
                    </Btn>
                  </div>
                </form>
              </FormContainer>
            </div>
            <Doneun
              src={env.PUBLIC_URL + "/assets/dongeun.png"}
              alt=" "
            />
          </BigBox>
        </Container>
      ) : (
        <Modal>
          <img src={env.PUBLIC_URL + "/assets/dongeun.png"} alt=" " />
          <h1>  !</h1>
          <h3>
            {" "}
             .
            <br />  !
          </h3>
        </Modal>
      )}
    </Wrapper>
  );
};

export default Letter;
