import { motion } from "framer-motion";
import { useState } from "react";
import styled from "styled-components";
import Characters from "../components/Characters";
import SubTitle from "../components/SubTitle";

const env = process.env;
env.PUBLIC_URL = env.PUBLIC_URL || "";

const Wrapper = styled.section`
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  padding: 100px 0px;
  @media ${(props) => props.theme.device.tablet} {
    padding: 70px 0px;
    .buddies {
      width: 500px;
    }
  }
  @media ${(props) => props.theme.device.mobile} {
    padding: 30px 0px;
    .buddies {
      width: 300px;
    }
  }
`;

const TextBox = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 50px;
  margin-bottom: 50px;
  font-weight: 700;
  font-size: 36px;
  line-height: 160%;
  @media ${(props) => props.theme.device.tablet} {
    font-size: 30px;
    line-height: 160%;
  }
  @media ${(props) => props.theme.device.mobile} {
    font-size: 20px;
    line-height: 150%;
  }
`;

const Btn = styled.span`
  width: 388px;
  height: 96px;
  font-size: 45px;
  font-weight: 700;
  line-height: 160%;
  border-radius: 50px;
  margin-top: 50px;
  background-color: ${(props) => props.theme.yellow};
  border: 3px solid #150b05;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
`;

const Symbol = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  div {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 50px;
    flex-direction: column;
    &:last-child {
      margin-right: 0;
    }
  }
  img {
    border-radius: 50%;
    background-color: ${(props) => props.theme.black};
    background-position: center center;
    background-size: cover;
    padding: 2rem;
  }
  span {
    margin-top: 10px;
    padding: 5px 10px;
    background-color: ${(props) => props.theme.black};
    color: white;
    border-radius: 25px;
    font-size: 20px;
  }
  @media ${(props) => props.theme.device.tablet} {
  }
  @media ${(props) => props.theme.device.mobile} {
    div {
      margin-right: 20px;
      &:last-child {
        margin-right: 0;
      }
    }
    img {
      width: 70px;
      height: 70px;
      border-radius: 50%;
      background-color: ${(props) => props.theme.black};
      background-position: center center;
      background-size: cover;
      padding: 0.7rem;
    }
    span {
      font-size: 12px;
    }
  }
`;

const MapBox = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  div {
    width: 1116px;
    height: 875px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  img {
    position: absolute;
    width: 100px;
    height: 100px;
    z-index: 5;
  }
  @media ${(props) => props.theme.device.tablet} {
  }
  @media ${(props) => props.theme.device.mobile} {
    div {
      width: 800px;
      height: 575px;
    }
    img {
      display: none;
    }
  }
`;

const Svg = styled(motion.svg)`
  @media ${(props) => props.theme.device.tablet} {
    width: 800px;
  }
  @media ${(props) => props.theme.device.mobile} {
    width: 500px;
  }
`;

